
/* font-family: 'Montserrat', sans-serif;
font-family: 'Oswald', sans-serif; */


*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Montserrat', sans-serif;
    font-weight: 700
}

.container{
    
    display: -webkit-flex;
    
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    height: 100vh;
    text-align: center;
    overflow-x: hidden;
    background-image: url(/WeatherApp/static/media/sky-49520_640.a69d4d3d.jpg);
    background-repeat: no-repeat;
    background-size: cover
}
.container h1{
    margin: 5px 0 10px 0;
    font-family: 'Oswald', sans-serif;
}
.city{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    width: 80vw;
    margin: 5px auto 0;
     height: 90vh; 
     /* height delate in land space */
}
.searchingPanel{
    display: inline-block;
    margin: 20px 0
}
.city input{
    height: 30px;
    width: 70%;
    text-align: center;
    border: none;
    outline: none;
    border-radius: 25px 0 0 25px;
    background: burlywood
}
.city button{
    height: 30px;
    width: 30%;
    font-size: 13px;
    border-radius: 0 25px 25px 0;
    border: none;
    outline: none; 
    background: #ffc107;
    cursor: pointer;
}
footer{
 height: 10vh;
 line-height: 8vh;
 text-align: center;
 font-size: 15px;  
}
footer p:nth-of-type(2){
    font-size: 7px;
    line-height: 0px;
    margin: 0;
}


.weatherInfo p {
    margin: 5px 0;
    letter-spacing: 1.5px
}

.imgWeather img{
    height: 20vh;
}
